$watch-size: 500px;
$watch-border: $watch-size * 0.4 / 100;

$hand-minute-width: $watch-size * 0.02;
$hand-minute-height: $watch-size * 0.3;

$hand-second-width: $watch-size * 0.01;
$hand-second-height: $watch-size * 0.45;

$hand-border: 45%;

$watch-center: $watch-size * 0.04;

$main-color: #3d4e61;
$second-color: #606e80;
